@import './_module_smallscreen.css';

.light-text {
	color: #efefef;
}

.seo {
	--light-text: #efefef;
	--dark-gray: #6f6f6f;
	--dark-text: var(--dark-gray);
	--default-text-color: #4d5156;
	--copy-button-bg-color: #ffc600;
	--mid-gray: #bbb;
	/* tabs variable properties */
	--tab-text-color: rgb(0, 155, 155);
	--tab-bg: #ffc600;
	--tab-active-bg: #272822;
	--tab-bg-hover: var(--tab-active-bg);
	--tab-text-hover-color: var(--light-text);
	--content-color: rgb(82, 75, 75);
	--all-tabs-bg: rgba(0 0 0/0.8);
	--tab-border-color: #fff;
	--tab-border-thickness: 2px;
	--tab-border-standard: var(--tab-border-thickness) solid var(--tab-border-color);


	--note-color: #b30000;
	--instructions-text-color: var(--dark-gray);
	--copy-button-color: #fff;

	/* used in accent boxes - but redefined in each type of box */
	--base-color: #b30000;
	/* instructions variables */
	--instructions-font-size: 18px;
	--instructions-text-color: #000;
}


.tabs__tab {
	padding: 1.2em;

	& > span {
		color: var(--instructions-text-color);
		font-family: monospace, sans-serif;
		line-height: 1.2;


		& > dl {
			color: var(--dark-text);
		}

	}
}


/* begin isntructions */

.instructions {
	font-size: var(--instructions-font-size);
	font-family: 'fira code', monospace, sans-serif;
	line-height: 1.2;
	font-weight: 300;
	margin-left: 1em;
	color: var(--dark-text);


	& a {
		font-weight: 700;
		letter-spacing: 1px;
	}
	& > span {
		font-style: italic;
		font-size: 0.6rem;

		&:first-of-type {
			font-weight: 700;
			font-size: 0.9rem;
			display: block;
		}

	}

	/* Any definition list inside of the instructions */
	& dl {
		border: 2px solid var(--mid-gray);
		font-size: 85%;
		line-height: 1.2;
		padding: 8px;
		background: #efefef;

		& dt {
			font-weight: 700;
			margin-top: 1.5em;
		}
		& dd {
			margin: 4px 28px;
			font-weight: 300;
			border-left: 2px solid var(--mid-gray);
			padding-left: 1em;
			line-height: 1.6;

			&.flex {
				display: flex;
				align-items: center;
			}

			& img {
				display: inline;
				width: 35%;
				border: 6px solid white;
				outline: rgba(0, 0, 0, 0.1) solid 1px;
				box-shadow: 2px 4px 3px rgba(0 0 0/0.3);
				margin: 1.2em;
			}
		}
	}
}

/* BOX */
.box {
	--base-color: #b30000;
	color: var(--default-text-color);
	border: 1px solid var(--base-color);
	padding: 32px 20px 14px;
	display: block;
	position: relative;
	margin: 1em 1.2rem;
	width: 80%;
	line-height: 1.4;
	font-size: 110%;

	&:before {
		position: absolute;
		content: "";
		background: var(--base-color);
		color: var(--light-text);
		left: -6px;
		padding: 2px 10px;
		top: -6px;
		font-weight: 700;
	}

		/* note type of box */
	&.note {
		--base-color: #784f1b;
		&:before {
			content: "note";

		}
	}
	/* END NOTE */

	/* alert type of box */
	&.alert {
		--base-color: #3229b6;
		&:before {
			content: "ALERT";
		}
	} /* END ALERT BOX */

	/* reminder type of box */
	&.reminder {
		--base-color: #1b784f;
		&:before {
			content: "REMINDER";
		}
	} /* END ALERT BOX */

	/* location type of box */
	&.location {
		--base-color: #781b44;
		&:before {
			content: "PASTE TO";
		}
	} /* END location BOX */

	/* reminder type of box */
	&.explanation {
		--base-color: #7f60da; /* a lighter purple */
		font-size: 1.1rem;
		width: 100%;
		&:before {
			content: "More Info";
		}
	} /* END ALERT BOX */
}
/* END BOXES */






/* END INSTRUCTION STYLES */



/*container for the language & 'copy' button */
div.code-toolbar > .toolbar {
	position: absolute;
	z-index: 10;
	top: .3em;
	right: .2em;
	transition: opacity 0.3s ease-in-out;
	opacity: 1;
}

/* initial button just says what language is being used -- hide it so that it will not confuse */
.toolbar-item:first-child {
	visibility: hidden;
	background: var(--copy-button-bg-color);
}

div.code-toolbar > .toolbar > .toolbar-item > a,
div.code-toolbar > .toolbar > .toolbar-item > button,
div.code-toolbar > .toolbar > .toolbar-item > span {
	color: var(--copy-button-color);
	font-size: .8em;
	padding: 0.2em .5em;
	/* background: #f5f2f0; */
	/* background: var(--copy-button-bg-color); */
	background: rgba(224, 224, 224, 0.8);
	font-weight: 300;
	box-shadow: 0 2px 0 0 rgba(0,0,0,0.2);
	text-shadow: 2px 1px 1px rgba(0,0,0,0.6);
	border-radius: 0.5em;
	font-family: monospace;
	text-transform: lowercase;
	/* transform: translateY(-4px); */
	line-height: 1;
	-webkit-transition: width 1s ease;
	transition: all 0.4s ease-out;

	&:hover {
		background: #0273b9;
		color: #fff;
	}
}


pre[class*="language-"] {
	margin-bottom: min(5vw,40px);
}


/*tabs*/
.wrapper.has-tabs {
	padding-left: 0;
	padding-right: 0;
}

.has-tabs {
	/* background: var(--tab-bg); */
	padding-bottom: 4vmax;
	border: 2px solid var(--mid-gray);
	min-height: 1200px;
}

[data-tab-info] {
	display: none;
}

.active[data-tab-info] {
	display: block;
}

.tab-content {
	font-size: 1.6em;
	color: var(--content-color);
}

/* top line */
.tabs {
	color: var(--light-text);
	background: var(--all-tabs-bg);
	display: flex;
	justify-content: space-around;
	margin: 0;
	width: 100%;
	margin-bottom: min(40px, 6vw);
}

.tabs > span {
	flex: 1 1 100px;
	background: rgb(28, 145, 38);
	padding: 10px;
	border-left: var(--tab-border-standard);
	border-top: var(--tab-border-standard);

	&:last-of-type {
		border-right: var(--tab-border-standard);

	}
}

.tabs span.active,
.tabs span:hover {
	background: var(--tab-bg-hover);
	color: var(--tab-text-hover-color);
}
.tabs span:hover {
	cursor: pointer;
}

